.testimonials {
  padding-top: 60px;
  padding-bottom: 100px;
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 112px;
    gap: 40px;
    @include tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__title {
    margin: 0;
    max-width: 530px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    color: var(--black);
    @include mobile {
      max-width: 247px;
      font-size: 24px;
      line-height: 34px;
    }
  }

  &__text {
    margin: 0;
    max-width: 376px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    @include mobile {
      max-width: 229px;
      font-size: 14px;
      line-height: 20px;
    }
  }
}
.testimonials-slider {
  position: relative;
  padding-left: 170px;
  @include tablet {
    padding-left: 70px;
  }
  @include small-tablet {
    padding-left: 0px;
  }
  &::after {
    content: "";
    position: absolute;
    top: -80px;
    left: 0;
    width: 117px;
    height: 77px;
    background-image: url(../img/testimonials-img.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 50;
  }
  &__rating {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-bottom: 16px;
  }

  &__rating-num {
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: var(--neutral-100);
  }

  &__text {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 500;
    font-size: 40px;
    line-height: 130%;
    color: var(--neutral-100);
    @include small-tablet {
      font-size: 18px;
      line-height: 26px;
    }
  }

  &__avtor {
    display: flex;
    align-items: center;
    gap: 24px;
  }

  &__avtor-name {
    margin: 0;
    margin-bottom: 4px;
    font-weight: 600;
    font-size: 18px;
    line-height: 26px;
    color: var(--neutral-100);
  }

  &__avtor-descr {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: var(--neutral-60);
  }

  &__btn {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 50;
    display: flex;
    gap: 16px;
    @include mobile {
      bottom: -70px;
    }
  }

  &__btn-item {
    background: var(--neutral-100);
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px;
    svg {
      fill: var(--neutral-40);
      width: 14px;
      height: 12px;
    }
    &.swiper-button-disabled {
      background: var(--neutral-10);
      border: 1px solid var(--neutral-40);
      pointer-events: none;
    }
  }

  &__btn-item--next {
    transform: rotate(180deg);
  }
}
