.services {
  padding: 100px 0;
  @include tablet {
    padding: 50px 0;
  }
  &__content {
    max-width: 634px;
    margin-bottom: 80px;
    @include mobile {
      max-width: none;
      margin-bottom: 40px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    color: var(--neutral-100);
    @include mobile {
      max-width: none;
      font-size: 24px;
      line-height: 34px;
    }
  }

  &__text {
    margin: 0;
    max-width: 462px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    @include mobile {
      max-width: none;
      font-size: 16px;
      line-height: 34px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
    gap: 64px;
    @include tablet {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}
.services-card {
  &__link {
    display: flex;
    align-items: center;
    gap: 60px;
    @include tablet {
      gap: 16px;
    }
  }

  &__img {
    position: relative;
    &--item {
      display: block;
      min-width: 110px;
      height: 100%;
    }
  }

  &__icon {
    position: absolute;
    top: 12px;
    right: 12px;
    backdrop-filter: blur(8px);
    background: rgba(22, 26, 52, 0.6);
    padding: 16px;
    border-radius: 50%;
    @include mobile {
      padding: 8px;
    }
  }

  &__text {
    max-width: 268px;
    @include tablet {
      max-width: 400px;
    }
    @include mobile {
      max-width: none;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    color: var(--neutral-100);
    @include mobile {
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: var(--neutral-60);
    @include mobile {
      font-size: 14px;
      line-height: 20px;
    }
  }
}
