.header {
  padding: 22px 0;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.menu {
  display: flex;
  align-items: center;
  @include tablet {
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100vh;
    z-index: 5;
    background-color: var(--neutral-100);
    flex-direction: column;
    align-items: flex-start;
    padding: 40px 10px;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;
  }
  &__link {
    position: relative;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: var(--black);
    @include tablet {
      color: var(--neutral-10);
    }
    &::after {
      content: "";
      position: absolute;
      bottom: -6px;
      left: 0;
      width: 0%;
      height: 2px;
      background-color: var(--black);
      transition: width 0.3s ease-in-out;
    }
    @include hover {
      &::after {
        width: 100%;
      }
    }
  }
}
.menu--active {
  transform: translate(0);
}
.nav {
  margin-right: 68px;
  @include tablet {
    margin-right: 0;
    margin-bottom: 40px;
  }
  &__list {
    display: flex;
    gap: 32px;
    @include tablet {
      flex-direction: column;
    }
  }
  &__link {
    position: relative;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: var(--neutral-100);
    @include tablet {
      color: var(--neutral-10);
    }
    &::after {
      content: "";
      position: absolute;
      bottom: -6px;
      left: 0;
      width: 0%;
      height: 2px;
      background-color: var(--black);
      transition: width 0.3s ease-in-out;
    }
    @include hover {
      &::after {
        width: 100%;
      }
    }
  }
}
.burger {
  display: none;
  @include burger;
  width: 24px;
  height: 18px;
  @include tablet {
    display: block;
  }
  &__line {
    height: 2px;
  }
}
