.pricing-hero {
  padding: 50px 0;
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__head {
    margin-bottom: 28px;
    padding: 4px 12px;
    display: inline-block;
    background-color: rgba(20, 99, 255, 0.1);
    border-radius: 16px;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: var(--primary-color);
  }

  &__title {
    margin: 0;
    margin-bottom: 56px;
    max-width: 905px;
    font-weight: 700;
    font-size: 72px;
    line-height: 120%;
    text-align: center;
    color: var(--neutral-100);
    @include tablet {
      font-size: 52px;
    }
    @include small-tablet {
      margin-bottom: 30px;
    }
    @include mobile {
      font-size: 32px;
    }
  }

  &__info {
    display: flex;
    justify-content: center;
    gap: 12px;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: var(--neutral-60);
    &::before {
      content: url(../img/toggle.svg);
      text-align: center;
    }
  }
}
