.forms {
  padding: 60px 0;
  position: relative;
  overflow: hidden;
  &__content {
    position: relative;
    z-index: 5;
    max-width: 610px;
    padding: 56px 48px;
    border: 1px solid var(--neutral-40);
    box-shadow: 0px 16px 24px 0px rgba(30, 30, 30, 0.06);
    background-color: var(--neutral-10);
    @include mobile {
      padding: 22px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 36px;
    line-height: 44px;
    color: var(--neutral-100);
    @include mobile {
      font-size: 18px;
      line-height: 26px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 48px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    @include mobile {
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__text-email {
    color: var(--primary-color);
  }

  &__img {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    @include small-tablet {
      display: none;
    }
  }
}
.contacts-forms {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  @include mobile {
    flex-direction: column;
  }
  &__top {
    display: flex;
    gap: 32px;
    @include mobile {
      flex-direction: column;
      gap: 0;
      margin-bottom: 24px;
    }
  }
  &__row {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 6px;
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: var(--neutral-100);
  }

  &__input {
    padding: 10px 14px;
    border: 1px solid var(--neutral-30);
    border-radius: 8px;
    box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);
    background-color: var(--neutral-10);
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: var(--black);
    &::placeholder {
      font-weight: 400;
      font-size: 14px;
      line-height: 24px;
      color: var(--neutral-60);
    }
  }
}
