.help {
  padding: 100px 0;
  background-color: var(--neutral-100);
  @include small-tablet {
    padding: 50px 0;
  }
  &__content {
    max-width: 816px;
    margin-bottom: 80px;
  }
  &__title {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    color: var(--neutral-10);
    @include small-tablet {
      font-size: 36px;
    }
    @include mobile {
      font-size: 24px;
    }
  }

  &__text {
    max-width: 660px;
    margin: 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    color: var(--neutral-60);
  }
}
.help-info {
  display: grid;
  grid-template-columns: repeat(2, 481px);
  justify-content: space-between;
  gap: 56px;
  @include tablet {
    grid-template-columns: repeat(1, 1fr);
  }
  &__item {
    padding-top: 65px;
    background-repeat: no-repeat;
    background-size: 40px;
    background-position: top left;
  }
  &__title {
    max-width: 283px;
    margin: 0;
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    color: var(--neutral-10);
  }

  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: var(--neutral-60);
  }

  &__item--1 {
    background-image: url(../img/info-icon-1.svg);
  }

  &__item--2 {
    background-image: url(../img/info-icon-2.svg);
  }

  &__item--3 {
    background-image: url(../img/info-icon-3.svg);
  }

  &__item--4 {
    background-image: url(../img/info-icon-4.svg);
  }
}
