.contact-hero {
  padding: 50px 0;
  &__title {
    margin: 0;
    margin-bottom: 100px;
    max-width: 741px;
    font-weight: 700;
    font-size: 72px;
    line-height: 120%;
    color: var(--neutral-100);
    @include mobile {
      font-weight: 700;
      font-size: 32px;
      line-height: 40px;
      color: var(--neutral-100);
    }
  }
}
.map {
  display: flex;
  justify-content: space-between;
  @include tablet {
    flex-direction: column;
  }
  &__content {
    margin-bottom: 40px;
  }
  &__info {
    max-width: 176px;
    @include tablet {
      display: flex;
      max-width: none;
      gap: 30px;
      flex-wrap: wrap;
    }
  }

  &__item {
    padding-bottom: 32px;
    border-bottom: 1px solid var(--neutral-50);
    &:not(:last-child) {
      margin-bottom: 40px;
      @include tablet {
        margin-bottom: 0;
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    color: var(--neutral-100);
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--neutral-60);
  }

  &__contact {
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    color: var(--neutral-100);
  }
}
