.insight {
  padding: 60px 0;
  &__title {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    text-align: center;
    color: var(--neutral-100);
    @include mobile {
      font-size: 24px;
      line-height: 34px;
      text-align: start;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 80px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
    color: var(--neutral-60);
    @include mobile {
      font-size: 16px;
      line-height: 24px;
      text-align: start;
    }
  }
}
.insight-slider {
  position: relative;
  &__slide {
    position: relative;
    @include small-tablet {
      width: auto;
    }
  }
  &__img {
    width: 100%;
    margin-bottom: 16px;
  }

  &__info {
    display: flex;
    justify-content: space-between;
    gap: 5px;
    margin-bottom: 16px;
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--neutral-60);
    @include mobile {
      font-size: 12px;
      line-height: 20px;
    }
  }
  &__link {
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    color: var(--neutral-100);
    &::after {
      content: "";
      position: absolute;
      inset: 0;
    }
    @include mobile {
      font-size: 16px;
      line-height: 24px;
    }
  }
  &__btn {
    position: absolute;
    top: 30%;
    left: 50%;
    z-index: 20;
    transform: translate(-50%, -50%);
    @include small-tablet {
      top: 0;
      left: 0;
      transform: translate(0);
      border-radius: 0;
      width: auto;
      height: auto;
      padding: 5px;
    }
  }
}
