.attraction {
  padding: 105px 0;
  @include small-tablet {
    padding: 50px 0;
  }
  &__title {
    margin: 0;
    margin-bottom: 118px;
    max-width: 960px;
    font-weight: 700;
    font-size: 72px;
    line-height: 120%;
    color: var(--neutral-100);
    @include mobile {
      margin-bottom: 48px;
      font-size: 32px;
      line-height: 40px;
    }
  }

  &__info {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @include small-tablet {
      flex-direction: column;
      gap: 45px;
    }
  }

  &__text {
    margin: 0;
    max-width: 738px;
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    color: var(--neutral-100);
    position: relative;
    @include mobile {
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__icon {
    position: relative;
    &-bg {
      animation: 10s linear 0s normal none infinite running rot;
    }
    &-arrow {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
@keyframes rot {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.attraction-slider {
  margin-top: 80px;
  padding-left: max(15px, calc((100% - 1230px) / 2));
  position: relative;
  @include mobile {
    padding: 0 15px;
  }
  &__slide {
    transition: height 0.3s ease-in-out;
    max-width: 502px;
    &.swiper-slide-next {
      .attraction-slider__img {
        transition: max-height 0.3s ease-in-out;
        max-height: 460px;
        @include mobile {
          max-height: 246px;
        }
      }
    }
    &.swiper-slide-prev {
      .attraction-slider__img {
        height: 622px;
        @include mobile {
          max-height: 246px;
        }
      }
    }
    &.swiper-slide-active {
      .attraction-slider__img {
        transition: max-height 0.3s ease-in-out;
        max-height: 370px;
        @include mobile {
          max-height: none;
          height: 246px;
        }
      }
      .attraction-slider__text {
        display: block;
        opacity: 1;
        pointer-events: painted;
      }
    }
  }
  &__img {
    transition: max-height 0.3s ease-in-out;
    display: block;
    width: 100%;
    height: 100%;
    max-height: 650px;
    margin-bottom: 24px;
    @include mobile {
      height: 246px;
    }
  }
  &__text {
    display: none;
    transition: opacity 0.3s ease-in-out;
    opacity: 0;
    pointer-events: none;
  }
  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    color: var(--neutral-100);
    @include mobile {
      font-size: 18px;
      line-height: 26px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 32px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    @include mobile {
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__btn {
    padding: 16px 34px;
    border-radius: 100px;
    background: var(--neutral-100);
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: var(--neutral-10);
  }
}
