.income {
  padding: 72px 0;
  &__container {
    display: flex;
    justify-content: space-between;
    gap: 40px;
    @include tablet {
      flex-direction: column;
    }
  }
  &__img {
    @include tablet {
      width: 100%;
    }
  }
  &__content {
    max-width: 557px;
    @include tablet {
      max-width: none;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    color: var(--neutral-100);
    @include small-tablet {
      font-size: 24px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 40px;
    max-width: 360px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    @include tablet {
      max-width: none;
    }
    @include small-tablet {
      font-size: 14px;
      line-height: 20px;
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 32px;
    @include mobile {
      gap: 16px;
    }
  }
  &__descr {
    margin: 0;
    padding-left: 45px;
    max-width: 393px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-100);
    background-image: url(../img/check.svg);
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 24px;
    @include tablet {
      max-width: none;
    }
    @include mobile {
      font-size: 14px;
      line-height: 20px;
    }
  }
}
