.hero {
  padding: 13px 0 141px 0;
  &__container {
    display: flex;
    align-items: center;
    @include tablet {
      flex-direction: column;
    }
  }

  &__text {
    max-width: 740px;
    position: relative;
    @include tablet {
      max-width: none;
    }
    &::after {
      content: "";
      pointer-events: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 100%;
      background: #fa7515;
      filter: blur(220px);
      opacity: 0.2;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 114px;
    font-weight: 700;
    font-size: 80px;
    line-height: 120%;
    color: var(--neutral-100);
    @include tablet {
      font-size: 52px;
    }
    @include small-tablet {
      margin-bottom: 60px;
    }
    @include mobile {
      font-size: 32px;
      margin-bottom: 40px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 69px;
    padding-left: 103px;
    position: relative;
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    color: var(--neutral-100);
    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 20px;
      width: 75px;
      height: 1px;
      background-color: var(--black);
      @include small-tablet {
        width: 24px;
        top: 11px;
      }
    }
    @include small-tablet {
      padding-left: 45px;
      font-size: 26px;
      line-height: 24px;
    }
    @include mobile {
      font-size: 16px;
      margin-bottom: 50px;
    }
  }
}
.hero-form {
  display: flex;
  @include small-mobile {
    flex-wrap: wrap;
    align-items: center;
  }
  &__input {
    flex-basis: 280px;
    padding-bottom: 23px;
    border-bottom: 1px solid var(--neutral-40);
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--neutral-60);
    outline: none;
    &::placeholder {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: var(--neutral-60);
    }
    @include tablet {
      flex-grow: 1;
    }
    @include mobile {
      padding-bottom: 16px;
      margin-right: 8px;
    }
    @include small-mobile {
      margin-bottom: 8px;
    }
  }

  &__btn {
    border-radius: 100px;
    padding: 16px 43px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: var(--neutral-10);
    background: var(--neutral-100);
    @include mobile {
      flex-shrink: 0;
      font-size: 12px;
      padding: 8px 16px;
    }
    @include small-mobile {
      flex-grow: 1;
    }
  }
}
