.blog-hero {
  padding: 70px 0;
  @include small-tablet {
    padding: 50px 0;
  }
  &__container {
    position: relative;
    &::after {
      content: "";
      pointer-events: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 100%;
      background: #fa7515;
      -webkit-filter: blur(220px);
      filter: blur(220px);
      opacity: 0.2;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 53px;
    font-weight: 700;
    font-size: 72px;
    line-height: 120%;
    color: var(--neutral-100);
    @include small-tablet {
      font-size: 32px;
      line-height: 40px;
    }
  }

  &__descr {
    margin: 0;
    max-width: 754px;
    padding-left: 110px;
    position: relative;
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    color: var(--neutral-100);
    @include small-tablet {
      padding-left: 42px;
      font-size: 16px;
      line-height: 24px;
    }
    &::before {
      content: "";
      position: absolute;
      top: 22px;
      left: 0;
      display: inline-block;
      width: 76px;
      height: 1px;
      background-image: url(../img/line.jpg);
      background-repeat: no-repeat;
      @include small-tablet {
        width: 24px;
        top: 12px;
      }
    }
  }
}
