.footer {
  padding-top: 112px;
  padding-bottom: 60px;
  &--main {
    background-color: var(--neutral-10);
  }
  &--second {
    background-color: var(--neutral-100);
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    margin-bottom: 52px;
    @include tablet {
      flex-direction: column;
    }
  }

  &__text {
    max-width: 720px;
  }

  &__title {
    margin: 0;
    margin-bottom: 32px;
    font-weight: 600;
    font-size: 64px;
    line-height: 120%;
    @include mobile {
      font-size: 32px;
    }
    &--main {
      color: var(--neutral-100);
    }
    &--second {
      color: var(--neutral-10);
    }
  }

  &__subtitle {
    margin: 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 34px;
    @include mobile {
      font-size: 18px;
    }
    &--main {
      color: var(--neutral-100);
    }
    &--second {
      color: var(--neutral-50);
    }
  }

  &__btn {
    padding: 16px 32px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    border-radius: 100px;
    &--main {
      color: var(--neutral-10);
      background-color: var(--neutral-100);
    }
    &--second {
      background-color: var(--neutral-10);
    }
  }

  &__center {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    padding: 72px 0;
    @include mobile {
      padding: 22px 0;
    }
    &--main {
      border-bottom: 1px solid var(--neutral-40);
    }
    &--second {
      border-bottom: 1px solid var(--neutral-70);
    }
    @include tablet {
      flex-direction: column;
    }
  }
}
.footer-info {
  max-width: 292px;
  &__logo {
    display: inline-block;
    margin-bottom: 4px;
  }
  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    &--main {
      color: var(--neutral-100);
    }
    &--second {
      color: var(--neutral-60);
    }
  }
}
.footer-menu {
  display: flex;
  gap: 93px;
  @include tablet {
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 44px;
  }
  &__title {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;

    &--main {
      color: var(--neutral-100);
    }
    &--second {
      color: var(--neutral-10);
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;

    &--main {
      color: var(--neutral-100);
    }
    &--second {
      color: var(--neutral-60);
    }
  }
}
.footer-bottom {
  padding: 45px 0 0 0;
  text-align: center;
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    &--main {
      color: var(--neutral-50);
    }
    &--second {
      color: var(--neutral-10);
    }
  }
}
