.marketing {
  padding-top: 65px;
  padding-bottom: 160px;
  @include tablet {
    padding: 50px 0;
  }
  &__list {
    margin-bottom: 80px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 34px;
    @include small-tablet {
      margin-bottom: 40px;
    }
  }

  &__item {
    grid-column: 3 span;
    position: relative;
    padding-right: 15px;
    @include tablet {
      grid-column: 6 span;
      &:nth-child(2) {
        &::after {
          display: none;
        }
      }
    }
    @include mobile {
      padding-right: 15px;
    }
    @include small-mobile {
      padding-bottom: 15px;
      grid-column: 12 span;
      border-bottom: 1px solid var(--neutral-40);
    }
    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        right: 0;
        top: 50%;
        width: 1px;
        height: 100%;
        max-height: 66px;
        transform: translateY(-50%);
        background-color: var(--neutral-40);
      }
    }
  }

  &__num {
    display: block;
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 64px;
    line-height: 120%;
    color: var(--neutral-100);
    @include mobile {
      font-size: 32px;
      line-height: 42px;
    }
  }

  &__info {
    margin: 0;
    max-width: 199px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    @include tablet {
      max-width: none;
    }
    @include mobile {
      font-size: 16px;
      line-height: 24px;
    }
  }
  &__img {
    width: 100%;
    margin-bottom: 80px;
    @include small-tablet {
      margin-bottom: 40px;
    }
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    gap: 40px;
    @include tablet {
      flex-direction: column;
    }
  }

  &__title {
    margin: 0;
    max-width: 772px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    color: var(--neutral-100);
    @include tablet {
      max-width: none;
    }
    @include small-tablet {
      font-size: 36px;
    }
    @include mobile {
      font-size: 24px;
      line-height: 34px;
    }
  }

  &__text {
    margin: 0;
    max-width: 431px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    @include tablet {
      max-width: none;
    }
    @include mobile {
      font-size: 14px;
      line-height: 20px;
    }
  }
}
