.team {
  padding: 160px 0;
  @include tablet {
    padding: 100px 0;
  }
  @include small-tablet {
    padding: 50px 0;
  }
  &__title {
    margin: 0;
    margin-bottom: 24px;
    max-width: 903px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    color: var(--neutral-100);
    @include mobile {
      font-size: 24px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 80px;
    max-width: 540px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    @include mobile {
      font-size: 16px;
    }
  }
}
.team-slider {
  margin-bottom: 105px;
  @include mobile {
    margin-bottom: 40px;
  }
  &__img {
    width: 100%;
    margin-bottom: 16px;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    margin-bottom: 4px;
  }

  &__name {
    margin: 0;
    margin-bottom: 4px;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: var(--neutral-100);
    @include mobile {
      font-size: 16px;
    }
  }

  &__list {
    display: flex;
    gap: 8px;
  }

  &__link {
    display: block;
    border-radius: 50%;
    background-color: var(--neutral-30);
    padding: 9px;
  }

  &__descr {
    margin: 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    color: var(--neutral-60);
    @include mobile {
      font-size: 14px;
    }
  }
}
.team-info {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  @include tablet {
    flex-direction: column;
  }
  &__title {
    margin: 0;
    max-width: 635px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    color: var(--neutral-100);
    @include tablet {
      max-width: none;
    }
    @include mobile {
      font-size: 24px;
    }
  }

  &__text {
    max-width: 506px;
    @include tablet {
      max-width: none;
    }
  }
  &__descr {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    @include mobile {
      font-size: 14px;
    }
  }
  &__link {
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    color: var(--neutral-100);
  }
}
