.pricing-card {
  padding: 70px 0;
  @include small-tablet {
    padding: 50px 0;
  }
  &__list {
    display: flex;
    justify-content: center;
    gap: 56px;
    @include tablet {
      flex-wrap: wrap;
    }
  }
}
.card-pricing {
  border-radius: 16px;
  padding: 40px 24px;
  max-width: 378px;
  &--active {
    background-color: var(--neutral-90);
  }
  &__head {
    margin-bottom: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
  }
  &__top {
    padding: 6px;
    background-color: var(--primary-color);
    border-radius: 100px;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: var(--neutral-10);
  }
  &__name {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 24px;
    line-height: 34px;
    color: var(--neutral-100);
    &--active {
      color: var(--neutral-10);
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 24px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: var(--neutral-60);
    &--active {
      font-size: 14px;
    }
  }

  &__price {
    display: block;
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    color: var(--neutral-100);
    &--active {
      color: var(--neutral-10);
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: var(--neutral-100);
    &--active {
      color: var(--neutral-10);
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__item {
    padding-left: 35px;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: var(--neutral-100);
    background-image: url(../img/check-1.svg);
    background-repeat: no-repeat;
    background-size: 24px;
    background-position: left center;
    &--active {
      color: var(--neutral-10);
    }
    &--closed {
      text-decoration: line-through;
      color: var(--neutral-60);
      background-image: url(../img/check-2.svg);
    }
  }
}
