.blog-slider {
  padding: 50px 0;
}
.blog-nav {
  margin-bottom: 80px;
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
  &__item {
    padding: 10px 14px;
  }
  &__btn {
    position: relative;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: var(--neutral-60);
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
    &--active {
      color: var(--neutral-100);
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: var(--neutral-100);
      }
    }
  }
}
.blog-article {
  &__img {
    width: 100%;
    height: 392px;
    @include mobile {
      height: 205px;
    }
  }

  &__info {
    padding-top: 16px;
    margin-bottom: 24px;
    display: flex;
    justify-content: space-between;
    gap: 5px;
    flex-wrap: wrap;
    @include small-tablet {
      display: none;
    }
  }
  &__content {
    padding-right: 24px;
  }
  &__data {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--neutral-60);
  }

  &__avtor {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--neutral-60);
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 32px;
    line-height: 40px;
    color: var(--neutral-100);
    @include small-tablet {
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    color: var(--neutral-60);
    @include small-tablet {
      font-size: 14px;
      line-height: 20px;
    }
  }
}
