.partners {
  padding: 64px 0;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    @include small-tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__content {
    max-width: 323px;
  }

  &__info {
    display: block;
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    color: var(--neutral-100);
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
  }

  &__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 730px;
  }
}
