.quote {
  padding: 80px 0;
  padding: 40px 0;
  &__container {
    display: flex;
    justify-content: space-between;
    @include small-tablet {
      flex-direction: column;
    }
  }

  &__tilte {
    margin: 0;
    margin-bottom: 42px;
    max-width: 480px;
    font-weight: 600;
    font-size: 50px;
    line-height: 120%;
    color: var(--neutral-100);
    @include mobile {
      margin-bottom: 16px;
      font-size: 24px;
      line-height: 34px;
    }
  }

  &__text {
    margin: 0;
    max-width: 470px;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: var(--neutral-100);
    @include mobile {
      font-size: 14px;
      line-height: 20px;
    }
  }
}
