.faq {
  padding: 60px 0;
  &__title {
    margin: 0;
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 56px;
    line-height: 120%;
    text-align: center;
    color: var(--neutral-100);
    @include mobile {
      font-size: 24px;
      line-height: 34px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 80px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
    color: var(--neutral-60);
    @include mobile {
      font-size: 16px;
      line-height: 24px;
    }
  }
}

.faq-accordion {
  &__item {
    border-bottom: 1px solid var(--neutral-30);
    padding-bottom: 32px;
    cursor: pointer;
    &:not(:last-child) {
      margin-bottom: 56px;
    }
    &.open {
      .faq-accordion__icon {
        transform: rotate(45deg);
        transition: transform 0.3s ease-in-out;
      }
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    gap: 34px;
  }

  &__title {
    margin: 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: var(--neutral-100);
    @include small-tablet {
      font-size: 16px;
    }
  }

  &__icon {
    position: relative;
    display: inline-block;
    max-width: 16px;
    width: 100%;
    height: 16px;
    transition: transform 0.3s ease-in-out;
    flex-grow: 1;
    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 100%;
      height: 2px;
      background-color: var(--neutral-100);
      transform: translate(-50%, -50%);
      transition: transform 0.3s ease-in-out;
    }
    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      width: 2px;
      height: 100%;
      background-color: #000;
      transform: translate(-50%, -50%);
      transition: transform 0.3s ease-in-out;
    }
  }

  &__content {
    overflow: hidden;
    max-height: 0;
    max-width: 641px;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    transition: max-height 0.3s ease-in-out;
    @include small-tablet {
      font-size: 14px;
    }
  }
}
