.articles {
  background: var(--neutral-100);
  padding: 120px 0;
  @include small-tablet {
    padding: 50px 0;
  }
}

.articles-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
  @include small-tablet {
    grid-template-columns: repeat(1, 1fr);
  }
  &__col {
    display: flex;
    flex-direction: column;
    gap: 140px;
    @include small-tablet {
      gap: 40px;
    }
  }

  &__body {
    position: relative;
    &:hover {
      .articles-content__name {
        text-decoration: underline;
      }
    }
  }
  &__img {
    width: 100%;
    margin-bottom: 24px;
  }
  &__name {
    display: inline-block;
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 40px;
    line-height: 120%;
    color: var(--neutral-10);
    &::after {
      content: "";
      position: absolute;
      inset: 0;
    }
    &::before {
      content: "";
      width: 0px;
      height: 1px;
      display: block;
      background: var(--neutral-10);
      transition: 300ms;
    }
    @include small-tablet {
      font-size: 18px;
      line-height: 26px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: var(--neutral-60);
    @include small-tablet {
      font-size: 14px;
      line-height: 20px;
    }
  }
}
